import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { appStaticText, isCompanyApp } from '../../shared/utils/app.static';
import { environment } from '../../../environments/environment';
import { CommonModule } from '@angular/common';
import { FormControl, FormGroup, FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HfButtonComponent, HfCardComponent, HfInputComponent, HfModelComponent, HfToastService, inputMsgType } from '@monorepo/ui';
import { URLS } from '../../shared/utils/urls';
import { CapitolcityoilHowitworksComponent, SecureHowitworksComponent } from '@monorepo/common-component';
